*{
  margin: 0;
  padding: 0;
}
body{
  background: rgb(2, 70, 196);
  height: 100vh;
  display: grid;
  place-content: center;
}
.App{
  width: 400px;
  min-height: 500px;
  background: rgb(14, 1, 87);
  border-radius: 10px;
}
.App header{
  width: 100%;

}
header form{
  width: 100%;
  margin: 20px auto;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.3em 0;
}
header form input{
  width: 200px;
  height: 45px;
  outline: none;
  border-radius: 10px;
  border: none;
  padding: 0 0.8em;
  background: rgb(3, 65, 122);
  color: white;
}
header form input::placeholder{
  color: rgba(255, 255, 255, 0.616);
}
header form button{
  width: 90px;
  height: 45px;
  outline: none;
  border: none;
  border-radius: 10px;
  margin-left: 0.4em;
  color: white;
  background: rgb(47, 0, 255);
  font-size: 1.4em;
  cursor: pointer;
}
.listblock{
  margin: 1em 0;
}
.list{
  width: 300px;
  height: 30px;
  background: rgb(212, 7, 178);
  margin: 0.5em auto;
  padding: 0.4em;
  color: white;
  border-radius: 10px;
}
.list p{
  font-size: 1.1em;
  position: relative;
}
.list p span{
  position: absolute;  
  top:50%;
  right: 10px;
  transform: translate(-50%,-50%);
  color: black;
  font-weight: bold;
  cursor: pointer;
}